<template>
    <section id="divFormBuilderlayout" class="main-content">
    <section class="main-content">
        <div class="col-md-12 mt-2">
            <div class="theme-primary partition  p-actions-expand">
                <span class="p-name text-white">{{ formName }}</span>
                <span class="p-actions float-right">
                    <a href="javascript:;" title="" @click="EditForm" class="p-action-btn text-white"><em aria-hidden="true" class="fa fa-pencil"></em><br>Edit</a>
                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                </span>
            </div>
        </div>
        <div class="container-fluid padding-t_10">
            <div class="left-menu-tab">
                <div class="accordion md-accordion left-filter" id="accordionEx" role="tablist" aria-multiselectable="true">
                    <div class="card">
                        <div class="card-header filter-head" role="tab" id="headingOne1">
                            <h5 class="mb-0">
                                <em class="fa fa-folder" aria-hidden="true"></em>
                                Fields Panel
                                <span class="expand_all_filters">
                                    <span data-target=".multi-collapse" data-toggle="collapse" aria-expanded="false" class="collapsed"><em class="fa fa-sort-desc" aria-hidden="true"></em></span>
                                </span>
                                <input type="hidden" id="ControllerName" value="client">
                                <input type="hidden" id="ActionName" value="Index">
                            </h5>
                        </div>
                    </div>
                    <div class="card-panel">
                        <div class="card">
                            <div class="card-header" role="tab" id="headingOne21">
                                <h5 class="mb-0">
                                    <a data-toggle="collapse" data-parent="#accordionEx" href="#collapseOne21" aria-expanded="false" aria-controls="collapseOne21" class="collapsed">
                                        <em class="fa fa-folder-open" aria-hidden="true"></em><span class="">Custom Fields</span>
                                        <em class="fa fa-angle-down rotate-icon" aria-hidden="true"></em>
                                    </a>
                                    <a href="javascript:;" style="display:none;" class="tab-Search-btn"><em aria-hidden="true" class="fa fa-search"></em></a>
                                </h5>
                            </div>
                            <div id="collapseOne21" class="multi-collapse collapse" role="tabpanel" aria-labelledby="headingOne21" data-parent="#accordionEx" style="">
                                <div class="card-body pt-0">
                                    <div class="w-100">
                                        <draggable  v-model="FiledList" element="ul" class="list-group ulparted pb-0 w-100" v-bind:options="{group:{ name:'fieldList',pull:'clone', put:false },sort:false}" @end="onEndDrag" @change="log($event)">

                                            <li class="list-group-item mb-2" v-bind:groupid="item.group_id" v-bind:id="item.type" v-bind:title="item.name" v-for="(item, index) in FiledList" v-bind:key="index">
                                                <em v-bind:class="item.classname"></em> {{item.name}}
                                                <input type="hidden" v-bind:value="item.group_id" v-if="item.type=='new'" />
                                            </li>
                                        </draggable>
                                        <div class="text-center"  v-if="FiledList.length == 0"><small class="text-danger">No Record found</small></div>

                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="card">
                            <div class="card-header" role="tab" id="headingOne18">
                                <h5 class="mb-0">
                                    <a data-toggle="collapse" data-parent="#accordionEx" href="#collapseOne18" aria-expanded="false" aria-controls="collapseOne18" class="collapsed">
                                        <em class="fa fa-folder-open" aria-hidden="true"></em><span class="">System Fields</span>
                                        <em class="fa fa-angle-down rotate-icon" aria-hidden="true"></em>
                                    </a>
                                    <a href="javascript:;" style="display:none;" class="tab-Search-btn"><i aria-hidden="true" class="fa fa-search"></i></a>
                                </h5>
                            </div>
                            <div id="collapseOne18" class="multi-collapse collapse" role="tabpanel" aria-labelledby="headingOne18" data-parent="#accordionEx" style="">
                                <div class="card-body pt-0">
                                    <div class="w-100">
                                        <draggable v-model="systemFiledList" class="list-group ulparted pb-0 w-100" v-bind:options="{group:{ name:'fieldList',pull:'clone', put:false },sort:false}" @@end="endDrag" @@change="log($event)">

                                            <div class="list-group-item mb-2" v-bind:groupid="item.group_id" v-bind:id="item.dt_code" v-bind:title="item.name"
                                                 v-for="(item, index) in systemFiledList" v-if="item.dt_code != 'new'" :key="index">
                                                <em v-bind:class="item.classname"></em> {{item.display_name}}
                                                <input type="hidden" v-bind:value="item.group_id" v-if="item.dt_code=='new'" />
                                            </div>

                                        </draggable>
                                        <div class="text-center" v-if="systemFiledList.length == 0"><small class="text-danger">{{$t('NoRecordfound')}}</small></div>
                                    </div>
                                </div>
                            </div>
                        </div> 
                    </div>
                </div>
                <div class="bottom_filter_button"><a href="javascript:;" data-target=".multi-collapse" data-toggle="collapse" aria-expanded="true" class="collapsed">Expand All or Collapse All</a></div>
            </div>
            <div class="right-content">
                <div class="listing">
                    <div class="expand-list">
                        <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                            <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                        </a>
                    </div>
                    <draggable class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0" v-model="LayoutList" style="min-height:20px;" v-bind:options="{group:{ name:'fieldList1' }}">
                        <div class="col-md-12 float-left pb-3 mb-3" v-for="(itemHeader, parentHeaderIndex) in LayoutList" :key="parentHeaderIndex">
                            <!----Header Section Start----->
                            <div class="mcf-head float-left border d-flex px-0 py-2" v-bind:headerId="itemHeader.headerId">
                                <div class="col-md-11">
                                    <div class="row" v-bind:class="{'justify-content-start':itemHeader.headerPositionType=='left', 'justify-content-center': itemHeader.headerPositionType =='center', 'justify-content-end': itemHeader.headerPositionType =='right'}">
                                        <div class="col-md-4">
                                            <input type="text" v-model.trim="itemHeader.header_name" placeholder="Enter Header Name" class="form-control" />
                                        </div>
                                    </div>
                                </div>
                                <div class="col-md-1 setlaping">
                                    <div class="field-action mt-2">
                                        <a href="javascript:;" class="action-icon" id="headerMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                            <i class="fa fa-cog text-dark" style="font-size:20px;"></i>
                                        </a>
                                        <div class="dropdown-menu panelfix rounded border bg-white" aria-labelledby="headerMenuLink">
                                            <h5>Selectlayout Type</h5>
                                            <div class="dropdown-divider p-0 m-0"></div>
                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-for="headerPosition in layoutPosition" :key="headerPosition">
                                                <label class="form-check-inline menu_widjet">
                                                    <input type="radio" class="chkitems" v-bind:value="headerPosition.value" v-model="itemHeader.headerPositionType" />
                                                    <span class="label-text mx-1">{{headerPosition.name}}</span>
                                                </label>
                                            </a>
                                            <a class="dropdown-item border-top py-2 pl-2 bg-light" href="javascript:;"
                                               v-on:click="RemoveHeader(itemHeader.headerId,parentHeaderIndex)">
                                                <i class="fa fa-trash text-danger" style="font-size:20px;"></i>
                                                <span class="ml-1 text-dark" style="font-size:14px;">Remove Header</span>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <!----Header Section End  ----->
                            <draggable class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0" style="min-height:20px;">
                                <div class="" v-for="(item, parentIndex) in itemHeader.SectionData" :key="parentIndex">
                                    <!--Dropable Section Part Start-->
                                    <div class="mcf-head float-left border-bottom">
                                        <input type="text" v-model.trim="item.group_display_name" placeholder="Enter Section Name"
                                               v-bind:class="{'form-control form-control-sm':true}" />

                                        <div class="field-action mt-2 setlaping">
                                            <a href="javascript:;" class="action-icon" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                <i class="fa fa-cog text-dark" style="font-size:20px;"></i>
                                            </a>
                                            <div class="dropdown-menu panelfix rounded border bg-white" aria-labelledby="dropdownMenuLink">
                                                <h5>Selectlayout</h5>

                                                <div class="dropdown-divider p-0 m-0"></div>
                                                <a class="dropdown-item px-2 py-1" href="javascript:;" v-for="layouttype in LayoutTypeList" :key="layouttype">
                                                    <label class="form-check-inline menu_widjet">
                                                        <input type="radio" class="chkitems" v-bind:value="layouttype.value" v-model="item.LayoutType" />
                                                        <span class="label-text mx-1">{{layouttype.name}}</span>
                                                    </label>
                                                </a>
                                                <a class="dropdown-item border-top py-2 pl-2 bg-light" href="javascript:;"
                                                   
                                                   v-on:click="RemoveLayout(item.group_id,parentHeaderIndex)">
                                                    <i class="fa fa-trash text-danger" style="font-size:20px;"></i>
                                                    <span class="ml-1 text-dark" style="font-size:14px;">Removelayout</span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                    <!--Dropable Section Part End  -->
                                    <div class="col-12 float-left p-1">
                                        <draggable class="col-12 float-left p-0 d-flex flex-wrap" 
                                                   v-bind:options="{group: 'fieldList'}" v-bind:groupid="item.group_id"
                                                   v-bind:systemgenerated="item.group_is_system_generated == false ? 0 : 1"
                                                   v-bind:fieldLength="item.FieldData.length"
                                                   v-model="item.FieldData" style="min-height:300px;"
                                                    v-on:change="dragProperty($event, parentIndex, parentHeaderIndex)"
                                                   >
                                            <div v-bind:class="{'col-sm-12 col-md-12 float-left p-2':true, 'col-lg-12 col-xl-12': item.LayoutType =='single', 'col-lg-6 col-xl-6': item.LayoutType =='double', 'col-lg-4 col-xl-4': item.LayoutType =='triple', 'col-lg-3 col-xl-3': item.LayoutType =='four','recent-drag': field.custom_field_id <= 0}"
                                                 v-for="(field, childIndex) in item.FieldData" v-bind:fielddata="field" v-show="field.is_include_in_post_jon == 'yes'"
                                                 v-bind:key="childIndex" v-bind:systemgenerated="item.group_is_system_generated == false ? 0 : 1" v-bind:fieldLength="item.FieldData.length">

                                                <div v-bind:class="{'pickflowbase ml-cutomf-bg float-left p-2 w-100':true,'field-required':(field.system_is_required==true || field.is_required==true),'box-warning': field.is_system_generated==0, 'border-danger':(duplicateNameExists==field.name)}">
                                                    <div class="labeltext text-left p-0 float-left">
                                                        <!--Label Name control Start-->
                                                        <label>{{$t(field.display_name==""?field.name:field.display_name)}}</label>
                                                        <!--Label Name control End  -->

                                                        <input type="text" v-bind:value="(field.name,field.display_name)"
                                                               v-bind:class="{'form-control form-control-sm':true}" v-if="field.is_system_generated>0 && field.dt_code =='text'" />

                                                        <input type="text" v-model="field.display_name"
                                                               v-bind:class="{'form-control form-control-sm':true}"
                                                               v-if="field.is_system_generated==0 && (field.dt_code !='checkbox' && field.dt_code !='textarea' && field.dt_code !='radio' && field.dt_code !='select' && field.dt_code !='img' && field.dt_code !='logo' && field.dt_code !='label')"
                                                               v-on:keyup="ChangeFieldName(field)" />

                                                        <label class="d-none">{{field.name}}</label>

                                                        <!--Show TextAres in Section Start--->
                                                        <textarea type="text" v-model="field.display_name"
                                                                  v-bind:class="{'form-control form-control-sm':true}"
                                                                  v-if="field.is_system_generated==0 && field.dt_code =='textarea'"
                                                                  v-on:keyup="ChangeFieldName(field)">
                                                        </textarea>
                                                        <!--Show TextAres in Section End--->
                                                        <!--Show Options for checkbox Start--->
                                                        <div class="custom-control custom-checkbox" v-for="(chkitem, index) in field.picklist_options"
                                                             v-if="field.dt_code =='checkbox' && field.picklist_options !='' && field.ChkBoxoptionsType=='VerticalCheckBox'">
                                                            <input type="checkbox" class="form-check-input"/>
                                                            {{chkitem}}
                                                        </div>
                                                        <div class="form-group" v-if="field.ChkBoxoptionsType=='HorizantalCheckBox'">
                                                            <div class="custom-control custom-checkbox custom-control-inline" v-for="(chkitem, index) in field.picklist_options"
                                                                 v-if="field.dt_code =='checkbox' && field.picklist_options !=''">
                                                                <input type="checkbox" class="form-check-input" />
                                                                {{chkitem}}
                                                            </div>
                                                        </div>
                                                        <!--Show Options for checkbox End--->
                                                        <!--Show Options for Radio Start --->
                                                        <div class="custom-control" v-for="(raditem, index) in field.picklist_options"
                                                             v-if="field.dt_code =='radio' && field.picklist_options !='' && !field.optionsType">
                                                            <input type="radio" name="radioA" class="form-check-input dynamic" />
                                                            {{raditem}}
                                                        </div>
                                                        <div class="custom-control p-0" v-if="field.optionsType">
                                                            <div class="custom-control custom-radio custom-control-inline" v-for="(raditem, index) in field.picklist_options"
                                                                 v-if="field.dt_code =='radio' && field.picklist_options !=''">
                                                                <input type="radio" name="radioA" class="form-check-input custom-control-input" />
                                                                {{raditem}}
                                                            </div>
                                                        </div>
                                                        <!--Show Options for Radio End  --->
                                                        <!--Show Options for Dropdown Start --->
                                                        <div class="custom-control" v-if="field.dt_code =='select'">
                                                            <select class="form-control chk" v-bind:class="{'form-control':true}"
                                                                    v-if="field.dt_code=='select' && field.picklist_options !=''">
                                                                <option value="">Select</option>
                                                                <option v-for="(dropitem, index) in field.picklist_options"
                                                                        v-bind:value="dropitem">
                                                                    {{dropitem}}
                                                                </option>
                                                            </select>

                                                        </div>
                                                        <!--Show Options for Dropdown End  --->
                                                        <!--Show Allowed File Type Start --->
                                                        <div class="custom-control" v-if="field.dt_code =='img' && field.picklist_options !=''">
                                                            <label>Allowed Files:</label>
                                                            <div v-for="(dropitem, index) in field.picklist_options" class="d-inline-block">
                                                                {{dropitem}},&nbsp;
                                                            </div>
                                                        </div>
                                                        <!--Show Allowed File Type End  --->

                                                    </div>



                                                    <div class="dropdown float-right pt-1 mr-3">
                                                        <a href="javascript:;" class="action-icon dotts" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                            <i class="fa fa-ellipsis-h text-secondary"></i>
                                                        </a>
                                                        <div class="dropdown-menu panelfix_dots rounded border bg-white" aria-labelledby="dropdownMenuLink">
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;">
                                                                <label class="form-check-inline" v-if="field.is_system_generated!=1">
                                                                    <input type="checkbox" v-bind:value="field.is_required" v-model="field.is_required" />
                                                                    <span class="label-text text-secondary mx-1" style="font-size:14px;">{{$t('MarkAsRequired')}}({{ field.is_system_generated }})</span>
                                                                </label>
                                                            </a>
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-model="EditProperty" v-bind:key="childIndex"
                                                               v-on:click="EditProperties(field,parentHeaderIndex,parentIndex,childIndex,$event)">
                                                                <em class="fa fa-eye text-primary" style="font-size:20px;"></em>
                                                                <span class="ml-1 text-secondary" style="font-size:14px;">{{(field.system_is_required == true && field.is_system_generated == true)?'View Properties':'Edit Properties'}}</span>
                                                            </a>   <div class="dropdown-divider m-0"></div>
                                                            <a class="dropdown-item px-2 py-1" href="javascript:;" v-on:click="RemoveField(item,childIndex,parentIndex, parentHeaderIndex)"
                                                               custom_field_id="field.custom_field_id" is_required="field.is_required" is_updated="field.is_updated"
                                                               v-if="field.is_system_generated==false">
                                                                <em class="fa fa-trash text-danger" style="font-size:20px;"></em>
                                                                <span class="ml-1 text-dark" style="font-size:14px;">{{$t('RemoveField')}}</span>
                                                            </a>
                                                        </div>
                                                    </div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="field.dt_code == 'string'" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa-font" style="font-size:20px;"></i></div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="field.dt_code == 'radio'" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa-dot-circle-o" style="font-size:20px;"></i></div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="field.dt_code == 'select'" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa fa-list" style="font-size:20px;"></i><select class="form-control form-control-sm d-none" style="width:50px;"><option>Select</option></select></div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="field.dt_code == 'date'" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa-calendar-o" style="font-size:20px;"></i></div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="(field.dt_code == 'int' || field.dt_code == 'bigint' || field.dt_code == 'decimal')" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa-sort-numeric-asc" style="font-size:20px;"></i></div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="field.dt_code == 'img'" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa-upload" style="font-size:20px;"></i></div>
                                                    <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;" v-if="field.dt_code == 'logo'" v-bind:title="GetFieldType(field.dt_code,field.validation_type)"><i class="fa fa-user-circle" style="font-size:20px;"></i></div>
                                                </div>
                                            </div>

                                        </draggable>
                                    </div>

                                </div>
                            </draggable>
                        </div>
                    </draggable>


                    <!--NewGroup, Save, Cancel,  Button Section Start-->
                    <div class="col-lg-12 p-0 text-right mt-3 float-left">
                        <a href="javascript:;" class="btn btn-primary mr-2" v-on:click="Preview()" title="Preview"><em class="fa fa-users pr-2"></em>{{$t('Preview')}}</a>
                        <a href="javascript:;" class="btn btn-primary mr-2" v-on:click="AddHeader()" title="Add Header"><em class="fa fa-users pr-2"></em>Add Header</a>
                        <a href="javascript:;" class="btn btn-primary mr-2" v-on:click="AddNewGroup()" title="New Group"><em class="fa fa-users pr-2"></em>Add Section</a>
                        <a href="javascript:;" v-bind:class="{'btn btn-success':true, 'disabled':(submitButtom==true)}" v-on:click="SubmitData()" title="Save" class="mr-2"><i class="fa fa-save pr-2"></i>{{$t('Save')}}</a>                        
                        <router-link to="/FormBuilder/Index" class="btn btn-danger formbtn widthhalf" title="Cancel"><em class="fa fa-close pr-2"></em>{{$t('Cancel')}}</router-link>
                    </div>
                </div>
            </div>
        </div>
        <modalEditPropertyVue v-if="showPropertyModal" @Close="Close" @SaveProperties="SaveProperties" :modal="poupConditionIndex"></modalEditPropertyVue>
        <AddFormVue v-if="showEditForm" :formName="formName"  :moduleId="moduleId" :subModuleId="subModuleId" @EditFormName="EditFormName" @Close="Close"></AddFormVue>
        <ViewFSMFormVue v-if="showPreview" formIdForShow="0"  @Close="Close" :listForSubmittedForm="previewData"></ViewFSMFormVue>
    </section>
</section>
</template>
<script>
import DataService from '../../services/DataService';
import { leftDraggableField } from "@/views/FormBuilder/vuedraggableForFormBuilder.js";
import modalEditPropertyVue from './modalEditProperty.vue';
import AddFormVue from './AddForm.vue';
import ViewFSMFormVue from '../../components/Common/ViewFSMForm.vue';
export default {
    components:{
        modalEditPropertyVue,
        AddFormVue,
        ViewFSMFormVue
    },
    data() {
        return {
            formId:0,
            showEditForm:false,
            showPreview:false,
            showPropertyModal:false,
            duplcateGroupNameExists:'',
            moduleId: 0,
            previewData:[],
            subModuleId: 0,
            systemFiledList:[],
            formType: null,
            formName: null,
            EditProperty: null,
            description: null,
            FiledList: [],
            LayoutList:[],
            headerId: 1,
            poupConditionIndex:null,
            addGroupId: 1,
            dropItems: [],
            duplicateNameExists: '',
            isGroupNameBlank:'',
            nonDisplayField:[],
            picklistOptionBlankItems:[],
            layoutPosition: [
                { name: "Right Side", value: "right" },
                { name: "Center", value: "center" },
                { name: "Left Side", value: "left" }],
            LayoutTypeList : [
                { name: "SingleColumn", value: "single" },
                { name: "DoubleColumn", value: "double" },
                { name: "TripleColumn", value: "triple" },
                { name: "FourColumn", value: "four" }]
            };
    },
    created: function () {
        var vm = this;
        vm.moduleId = vm.$route.params.ModuleId;
        vm.formId=vm.$route.params.FormId;
        vm.subModuleId = vm.$route.params.SubModuleId;
        vm.formType = vm.$route.params.FormType;
        vm.formName = vm.$route.params.FormName;
        vm.description = vm.$route.params.Desc;
        vm.FetchData();
        var filedList = leftDraggableField;
        vm.FiledList=filedList;
        vm.FetchSytemFieldList();
        if(vm.formId==0){
            //vm.AddHeader();
        }
        setTimeout(() => {
            vm.SetResponsiveTab();
            vm.RadioButtonBootstrap();
        }, 500);
    },
    methods:{
        FetchData: function () {
            debugger;
            var vm = this;
           // vm.LayoutList=[];
            var params=`id=${vm.formId}&moduleName=&subModuleCode=`
            DataService.GetFormById(params).then((response) => {
                this.FieldData = response.data.data;
                if (this.FieldData != null && this.FieldData.length > 0) {
                            this.formNameValue = this.FieldData[0].FormName;
                            this.descriptionValue = this.FieldData[0].Description;
                            this.formTypeValue = this.FieldData[0].FormType;
                            if (this.formTypeValue) {
                                this.moduleId22 = this.FieldData[0].ModuleID;
                                this.SubModuleId22 = this.FieldData[0].SubModuleId;
                                //this.FetchSytemFieldList(this.SubModuleId22);
                            }
                }
                this.LanguageArray = response.data.languages
                this.nonDisplayField = $.grep(this.FieldData, function (element, index) {
                    return element.form_field_visibility == 'NO';
                });
                var fieldData = $.grep(this.FieldData, function (element, index) {
                    return element.form_field_visibility == null;
                });
                this.FieldData = fieldData;
                var newLayoutIndex = this.FiledList.findIndex(function (item, i) {
                    return item.group_id == '-1';
                });
                if (this.FieldData != null && this.FieldData.length > 0) {
                            $.each(this.FieldData, $.proxy(function (index, itemHeader) {
                                var headerId = itemHeader.headerId;
                                vm.headerId = parseInt(headerId)+ 1;
                                var CheckHeader = $.grep(vm.LayoutList, function (element, index) {
                                    return element.headerId == headerId;
                                });
                                if (CheckHeader.length == 0) {
                                    var LayoutObject = {
                                        headerId: itemHeader.headerId,
                                        header_name: itemHeader.HeaderName,
                                        header_DisplayOrder: itemHeader.header_DisplayOrder,
                                        headerPositionType: itemHeader.headerPositionType,
                                        SectionData: [],
                                        footerData:[]
                                    }
                                    var returnAllGroupOfHeader = $.grep(vm.FieldData, function (element, index) {
                                        return element.headerId == headerId;
                                    });
                                    $.each(returnAllGroupOfHeader, $.proxy(function (index, itemGroup) {
                                        var groupId = itemGroup.group_id;
                                        vm.addGroupId = parseInt(groupId) + 1;
                                        var chkGroup = $.grep(LayoutObject.SectionData, function (element, index) {
                                            return element.group_id == groupId;
                                        });
                                        if (chkGroup.length == 0) {
                                            var returnAllFieldsOfGroup = $.grep(returnAllGroupOfHeader, function (element, index) {
                                                return element.group_id == groupId;
                                            });
                                            var objGroup = {
                                                group_id: groupId,
                                                headerId: itemGroup.headerId,
                                                group_name: this.GetLocalizedValue(itemGroup.group_name, itemGroup.group_display_name),
                                                group_display_name: this.GetLocalizedValue(itemGroup.group_name, itemGroup.group_display_name),
                                                group_is_system_generated: itemGroup.group_is_system_generated,
                                                LayoutType: itemGroup.layout_type,
                                                FieldData: returnAllFieldsOfGroup
                                            };
                                            LayoutObject.SectionData.push(objGroup);
                                            
                                        }
                                    }, this));
                                     vm.LayoutList.push(LayoutObject);
                                }
                               }, this));
                }
            })
        },
        Preview:function(){
            var vm=this;
            vm.showPreview=true;
        },
