            var vm=this;
            vm.showEditForm=true;
        },
        GetFieldType: function (name, type) {
            if (type == "email") {
                return type;
            }
            else {
                return name;
            }
        },
        EditProperties: function (field, parentHeaderIndex, parentIndex, childIndex, event) {
            this.poupConditionIndex=field;
            this.showPropertyModal = true;
            this.EditProperty = { Item: field, ParentHeaderIndex: parentHeaderIndex, ParentIndex: parentIndex, ChildIndex: childIndex };
            this.$validator.reset();
        },
        Close:function(){
            var vm=this;
            vm.showPropertyModal=false;
            vm.showEditForm=false;
            vm.showPreview=false;
        },
        dragProperty: function(x, sectionIndex, groupIndex){
            let obj = {...this.LayoutList[groupIndex].SectionData[sectionIndex].FieldData[x.added.newIndex]}
            this.previewData.push(this.LayoutList[groupIndex].SectionData[sectionIndex]);
            this.LayoutList[groupIndex].SectionData[sectionIndex].FieldData.splice(x.added.newIndex, 1)
            this.LayoutList[groupIndex].SectionData[sectionIndex].FieldData.splice(x.added.newIndex, 0, obj)
            setTimeout(() => {
                this.SetResponsiveTab();
                this.CheckBoxBootstrap();
                this.RadioButtonBootstrap();
            }, 500);
        },
        SaveProperties:function(item){
            var vm=this;
            this.LayoutList[vm.EditProperty.ParentHeaderIndex].SectionData[vm.EditProperty.ParentIndex].FieldData[vm.EditProperty.ChildIndex] = item;
            vm.showPropertyModal = false;
        },
        FetchSytemFieldList:function(){
            var vm=this;
            var params=`moduleName=${vm.moduleId}&subModuleCode=${vm.subModuleId}`
            DataService.GetSystenFiledList(params).then((response) => {
                vm.systemFiledList=response.data.data;
            })
        },
        RemoveField: function (item, childIndex, parentIndex, headerIndex) {
            var vm = this;
            var fullLayoutList = vm.LayoutList;
            console.log("fullLayoutList", fullLayoutList);
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Claim")), function () {
                if (fullLayoutList[headerIndex].SectionData[parentIndex].FieldData.length > 1) {
                    fullLayoutList[headerIndex].SectionData[parentIndex].FieldData.splice(childIndex, 1);
                } else {
                    fullLayoutList[headerIndex].SectionData[parentIndex].FieldData[childIndex].name = 'X' + fullLayoutList[headerIndex].SectionData[parentIndex].FieldData[childIndex].group_id;
                    fullLayoutList[headerIndex].SectionData[parentIndex].FieldData[childIndex].display_name = 'X' + fullLayoutList[headerIndex].SectionData[parentIndex].FieldData[childIndex].group_id;
                    fullLayoutList[headerIndex].SectionData[parentIndex].FieldData[childIndex].is_include_in_post_jon = 'no';
                }
            });
        },
        AddHeader: function () {
                var vm=this;
                var objHeader = {
                    headerId: vm.headerId,
                    header_name: '',
                    header_DisplayOrder: vm.headerId,
                    headerPositionType: 'left',
                    SectionData: [],
                    footerData: []
                };
                var FieldData = [];
                FieldData = [{ "display_order": 1, "name": "", "label": "", "display_name": "", "custom_field_id": null, "group_id": 30140, "group_name": "", "group_display_name": "", "group_is_system_generated": true, "data_type_id": 1, "data_type_name": "Text", "code": "string", "sql_type": "nvarchar", "form_field_visibility": "YES", "list_field_visibility": "YES", "length": 200, "is_required": false, "picklist_options": null, "optionsType": null, "ChkBoxoptionsType": null, "decimal_places": null, "dt_code": "text", "table_name": "talygen_crm_lead", "value": "", "select_value": "", "validation_type": null, "range_from": null, "range_to": null, "regular_expression": null, "is_updated": 0, "is_inserted": 0, "is_include_in_post_jon": "yes", "is_system_generated": true, "group_display_order": 1, "layout_type": "double", "system_is_required": false, "field_code": null, "dropdown_type": null, "select_options": null, "parent_id": null, "dependent_type": null, "dependent_show_type": 1, "has_dependent": 0, "is_email_template": 1, "user_guide": null, "cultureDetails": "[]", "select_value": "[]" }];
                var lastObject = FieldData[0];
                lastObject = Object.assign({}, lastObject, {});
                lastObject.is_include_in_post_jon = 'no';
                var fieldData = [];
                fieldData.push(lastObject);
                var objGroup = {
                    group_id: vm.addGroupId,
                    headerId: vm.headerId,
                    group_name: '',
                    group_display_name: '',
                    group_is_system_generated: false,
                    LayoutType: 'double',
                    FieldData: fieldData
                };
                objHeader.SectionData.push(objGroup);
                vm.addGroupId += 1;
                vm.LayoutList.push(objHeader);
                vm.headerId += 1;
                console.log("LayoutListAfterAddHeader", vm.LayoutList);
            },
            endDrag: function () {
                this.FiledList.sort(dynamicSort("order"));
                setTimeout("CheckBoxBootstrap()", 100);
                setTimeout("RadioButtonBootstrap()", 500);
                setTimeout(function () {
                    $('#divmanageoagelayout').find('.recent-drag').removeClass("recent-drag");
                }, 3000);
                this.targetElement = null;
                this.sourceElement = null;
            },
            log: function (evt) {
                debugger;
                this.FiledList.sort(dynamicSort("order"));
            },
            RemoveLayout: function (groupId, parentHead) {
                var vm = this;
                var fullLayoutList = this.LayoutList;
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Group")), function () {
                    var chkGroup = $.grep(fullLayoutList[parentHead].SectionData, function (element, index) {
                        return element.group_id != groupId;
                    });
                    fullLayoutList[parentHead].SectionData = chkGroup;
                    vm.LayoutList = fullLayoutList;
                });
            },
            dragItems: function ($event) {
                this.dropItems.push(JSON.parse($event))
                this.DraggableForFormBuilder.onDragAdd;
            },
            SubmitData: function (e) {
                var vm = this;
                var fullJson = this.LayoutList;
                // if (fullJson[0].SectionData[0].FieldData.length == 1) {
                //   //debugger;
                //     vm.ShowAlert(vm.$t("Selectanyfield"), "warning", true, vm.$t("Alert"));
                //     return false;
                // }
                //vm.$validator.validate().then(function (valid) {
                        vm.submitButtom = true;
                        var mainJson = [];
                        var arrayNameField = [];
                        var arrayGroupNameField = []
                        vm.picklistOptionBlankItems = []
                        vm.duplcateGroupNameExists = '';
                        vm.duplicateNameExists = '';
                        vm.isGroupNameBlank = '';
                        $.each(this.LayoutList, function (index, element) {
                            if (arrayGroupNameField.includes(element.group_display_name) == true) {
                                vm.duplcateGroupNameExists = element.group_display_name;
                            } else {
                                arrayGroupNameField.push(element.group_display_name);
                            }
                            if (element.group_display_name == '') {
                                vm.isGroupNameBlank = element.group_name;
                            }
                            $.each(element.SectionData[index].FieldData, function (index1, tempItem) {
                                if ((tempItem.dt_code == 'select' || tempItem.dt_code == 'radio' || tempItem.dt_code == 'checkbox') && $.trim(tempItem.picklist_options) == '') {
                                    vm.picklistOptionBlankItems.push(tempItem.name);
                                }
                                if (arrayNameField.includes(tempItem.name) == true) {
                                    vm.duplicateNameExists = tempItem.name;
                                } else {
                                    arrayNameField.push(tempItem.name);
                                }
                            });
                           
                        });
                        var DisplayOrder = 0;
                        $.each(fullJson, function (itrHeader, itemHeader) {
                            var headerObj = itemHeader;
                            $.each(headerObj.SectionData, function (itr, item) {
                                var fieldData = item.FieldData;
                                var isUpdated = 0;
                                if (typeof (fieldData) != undefined && fieldData != null) {
                                    var chk_is_updated = $.grep(fieldData, function (element, index) {
                                        return (element.is_updated == 1 || element.is_inserted);
                                    });
                                    if (chk_is_updated.length > 0) {
                                        isUpdated = 1;
                                    }
                                }
                                var fieldDataList = null;
                                fieldDataList = $.grep(item.FieldData, function (element, index) {
                                    element.display_order = index + 1;
                                    return (element.is_include_in_post_jon == 'yes');
                                });
                                headerObj.SectionData[itr].FieldData = fieldDataList;
                            });
                            $.each(headerObj.footerData, function (itr, item) {
                                var fieldData = item.FieldData;
                                var isUpdated = 0;
                                if (typeof (fieldData) != undefined && fieldData != null) {
                                    var chk_is_updated = $.grep(fieldData, function (element, index) {
                                        return (element.is_updated == 1 || element.is_inserted);
                                    });
                                    if (chk_is_updated.length > 0) {
                                        isUpdated = 1;
                                    }
                                }
                                var fieldDataList = null;
                                fieldDataList = $.grep(item.FieldData, function (element, index) {
                                    element.display_order = index + 1;
                                    return (element.is_include_in_post_jon == 'yes');
                                });
                                headerObj.footerData[itr].FieldData = fieldDataList;
                            });
                            mainJson.push(headerObj);
                        });

                        if (vm.formId> 0) {
                            var postObject = {
                                formId: vm.formId,
                                moduleName: vm.moduleId,
                                subModuleCode: vm.subModuleId,
                                formName: vm.formName,
                                description: vm.description,
                                formTypeId: 1,
                                data: mainJson
                            };
                        }
                        else {
                            var postObject = {
                                formId: vm.formId,
                                moduleName: vm.moduleId,
                                subModuleCode: vm.subModuleId,
                                formName: vm.formName,
                                description: vm.description,
                                formTypeId: 1,
                                data: mainJson
                            };
                        }

                        if (vm.duplicateNameExists == '' && vm.duplcateGroupNameExists == '' && vm.isGroupNameBlank == '' && vm.picklistOptionBlankItems.length == 0) {
                            var objectPostString = JSON.stringify(postObject);
                            DataService.SaveEditFormBuilderForm(objectPostString).then((response) => {
                                this.ShowAlert("FormSavedSuccessfully", "success", true);
                                this.$router.push({path:"/FormBuilder/Index"});
                                this.submitButtom = false;
                            }, response => {
                                this.submitButtom = false;
                            });
                            this.submitButtom = false;
                        } else {
                            this.submitButtom = false;
                           this.ShowAlert(this.$t('CorrectHighlightedErrors'),'warning');
                        }
            },
            AddNewGroup: function () {
                var FieldData = [];
                this.isFooter = false;
                FieldData = [{ "display_order": 1, "name": "", "label": "", "display_name": "", "custom_field_id": null, "group_id": 30140, "headerId": "", "group_name": "", "group_display_name": "", "group_is_system_generated": true, "data_type_id": 1, "data_type_name": "Text", "code": "string", "sql_type": "nvarchar", "form_field_visibility": "YES", "list_field_visibility": "YES", "length": 200, "is_required": false, "picklist_options": null, "optionsType": null, "ChkBoxoptionsType": null, "decimal_places": null, "dt_code": "text", "table_name": "talygen_crm_lead", "value": "", "select_value": "", "validation_type": null, "range_from": null, "range_to": null, "regular_expression": null, "is_updated": 0, "is_inserted": 0, "is_include_in_post_jon": "yes", "is_system_generated": true, "group_display_order": 1, "layout_type": "double", "system_is_required": false, "field_code": null, "dropdown_type": null, "select_options": null, "parent_id": null, "dependent_type": null, "dependent_show_type": 1, "has_dependent": 0, "is_email_template": 1, "user_guide": null, "cultureDetails": "[]", "select_value": "[false]" }];
                var lastObject = FieldData[0];
                lastObject = Object.assign({}, lastObject, {});
                lastObject.is_include_in_post_jon = 'no';
                lastObject.name = "Section" + this.addGroupId;
                lastObject.display_name = "Section" + this.addGroupId;
                var fieldData = [];
                fieldData.push(lastObject);
                var objGroup = {
                    group_id: this.addGroupId,
                    group_name: '',
                    group_display_name: '',
                    group_is_system_generated: false,
                    LayoutType: 'double',
                    FieldData: fieldData,
                    headerId: this.headerId
                };
                this.LayoutList[this.LayoutList.length - 1].SectionData.push(objGroup);
                this.addGroupId += 1;
            },
            RemoveHeader: function (headerId, parentHead) {
                //debugger;
                var vm = this;
                var fullLayoutList = this.LayoutList;
                console.log("sdsadasd", fullLayoutList);
                vm.confirmR(vm.$t("ConfirmDeleteGroup"), true, (vm.$t("Group")), function () {
                        var chkHeader = $.grep(fullLayoutList, function (element, index) {
                            return element.headerId != headerId;
                        });
                        vm.LayoutList = chkHeader;
                });
            },
    }
}
