<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Properties') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                            <Form ref="AddFormBuilder">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left form-content py-2 p-0">
                                    <div class="col-12 float-left" v-bind:class="{'d-none':true}">
                                        <div class="form-group">
                                            <label>{{$t('fieldName')}}:<span class="text-danger">*</span></label>
                                            <Field name="FieldName" v-slot="{errors}" :rules="{ 'required': true }" >
                                                <input type="text"  v-bind:class="{'form-control': true}"
                                                    v-model="modal.name" id="txtmodal_name" />
                                                <span v-if="errors[0]" name="FieldName" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>

                                    <div class="col-12 float-left">
                                        <div class="form-group">
                                            <label>Label Name:<span class="text-danger">*</span></label>
                                            <Field name="LabelName" v-slot="{errors}" :rules="{ 'required': true }" >
                                                <input type="text" :class="{'form-control':true,'is-invalid':errors[0]}" v-bind:value="(modal.name,modal.display_name)" v-on:keyup="ChangeFieldName(modal)"
                                                    v-if="modal.is_system_generated > 0"
                                                    id="txtmodaldisplay_name"
                                                    placeholder="Enter Label Name"
                                                    />
                                                    <span v-if="errors[0]" name="LabelName" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                            <Field name="LabelName" v-slot="{errors}" :rules="{ 'required': true }" >
                                                <input type="text" :class="{'form-control':true,'is-invalid':errors[0]}" v-model.trim="tempDisplayName" v-on:keyup="ChangeFieldName(modal)"
                                                    v-if="modal.is_system_generated == 0" id="txtmodaldisplay_name"
                                                    />
                                                    <span v-if="errors[0]" name="LabelName" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                            <div class="invalid-feedback" style="display:none;" id="displayNameError">{{displayMessage}}</div>
                                        </div>
                                    </div>
                                    <div class="col-12 float-left" v-if="modal.dt_code !='select' && modal.dt_code !='radio' && modal.dt_code !='img' && modal.dt_code !='logo' && modal.dt_code !='checkbox' && modal.dt_code !='date' && modal.is_system_generated <= 0">
                                        <div class="form-group">
                                            <label>Number of characters allowed:<span class="text-danger">*</span></label>
                                            <Field name="Number of characters allowed" v-slot="{errors}" :rules="{ 'required': true }" >
                                                <input type="text" data-numberonly='yes' :class="{'form-control':true,'is-invalid':errors[0]}"
                                                   v-model.number="tempLength" class="form-control" id="txtmodallength" name="Length" />
                                                <span v-if="errors[0]" name="Number of characters allowed" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-md-6 float-left" v-if="(modal.dt_code =='select' || modal.dt_code =='radio' || modal.dt_code =='checkbox')">
                                        <div class="form-group mb-0">
                                            <label>Options:<span class="text-danger">*</span></label>
                                            <Field name="Options" v-slot="{errors}" :rules="{ 'required': true }" >
                                            <input type="text" :class="{'is-invalid':errors[0]}" class="form-control" id="txtpicklist_options" v-bind:name="modal.picklist_options"
                                                   v-model="tempPicklistOption"  placeholder="Option1" />
                                                   <span v-if="errors[0]" name="Options" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-md-6 float-left" v-if="modal.dt_code=='radio'">
                                        <label>Options Type:<span class="text-danger">*</span></label>
                                        <div class="form-group">
                                            <label class="switch large">
                                                <input class="form-check-input rdoticketfor custom-control-input dynamic" type="checkbox"
                                                       
                                                       v-bind:checked="(modal.optionsType == 'Horizantal')" v-on:change="AssignOptionsTypeToggleValue($event)">
                                                <span class="slider round">
                                                    <span class="slider-yes">Horizantal</span>
                                                    <span class="slider-no"> Vertical</span>
                                                </span>
                                            </label>
                                        </div>
                                    </div>
                                    <div class="col-md-6 float-left" v-if="modal.dt_code=='checkbox'">
                                        <label>Options Type:<span class="text-danger">*</span></label>
                                        <div class="form-group">
                                            <label class="switch large">
                                                <input class="form-check-input rdoticketfor custom-control-input dynamic" type="checkbox"
                                                      
                                                       v-bind:checked="(modal.ChkBoxoptionsType == 'HorizantalCheckBox')" v-on:change="AssignCheckboxOptionsTypeToggleValue($event)">
                                                <span class="slider round">
                                                    <span class="slider-yes">Horizantal</span>
                                                    <span class="slider-no"> Vertical</span>
                                                </span>
                                            </label>
                                        </div>
                                    </div>

                                    <div class="col-12 float-left p-0" v-if="modal.is_system_generated == false && (modal.dt_code =='checkbox' || modal.dt_code =='radio' || modal.dt_code =='select')">
                                        <div class="collapse show col-12 p-0">
                                            <div v-if="modal.is_system_generated == false && (modal.dt_code =='checkbox' || modal.dt_code =='radio' || modal.dt_code =='select')" class="validationfixer col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                <div v-for="(item, index) in diplayNameWithCulture" class="step1fixer col-sm-12 float-left col-md-6 col-lg-6 col-xl-6 mb-2" :key="index">
                                                    <div v-bind:class="{'col-sm-12 mt-2 float-left':true, 'col-md-3 col-lg-3 col-xl-3':(isUserGuide==true), 'col-md-12 col-lg-12 col-xl-12 p-0':(isUserGuide!=true)}">
                                                        <Field v-bind:name="'Option' + index" v-slot="{errors}" :rules="{ 'required': true }" >
                                                            <div class="input-group">
                                                                <input type="text" :class="{'is-invalid':errors[0]}" v-model="item.localization_value" v-validate="'required'" id="txtpicklist_options2" class="form-control" v-bind:name="'value' + index" />
                                                                <div class="input-group-append">
                                                                    <span class="input-group-text"><a href="javascript:;" v-on:click="removeCultureRow(index)"><i class="fa fa-trash-o action-icon text-danger"></i></a></span>
                                                                </div>
                                                            </div>
                                                            <span v-if="errors[0]" v-bind:name="'Option' + index" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                        </Field>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="float-right mb-4 col-12">
                                            <a href="javascript:;" class="btn btn-success" v-on:click="addMoreRowForCulture()"><i class="fa fa-plus"></i> Add More</a>
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3 float-left mt-2 d-none">
                                        <div class="form-group">
                                            <div class="mt-2 float-left text-danger">{{$t('CorrectHighlightedErrors')}}</div>
                                        </div>
                                    </div>

                                    <div class="col-lg-12 float-left">
                                        <div class="row flex-row-reverse">
                                            <div class="col-lg-6 text-right">
                                                <a v-if="(modal.system_is_required == false || modal.is_system_generated == false)" href="javascript:;"
                                                   class="btn btn-success formbtn widthhalf mr-2" v-on:click="onSubmit(modal)" title="@Resources.Save">
                                                    <i class="fa fa-save pr-2"></i>{{$t('Save')}}
                                                </a>
                                                <a href="javascript:;" v-on:click="$emit('Close')" class="btn btn-danger formbtn widthhalf" title="@Resources.Cancel">
                                                    <i class="fa fa-close pr-2"></i>{{$t('Cancel')}}
                                                </a>
                                            </div>
                                            <div class="col-lg-6 text-danger">{{$t('MandatoryString')}}</div>
                                        </div>
                                    </div>
                                </div>
                            </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        modal:{
            required:false
        }
    },
    data() {
        return {
            isLoading:false,
            TimeZoneList:[],
            ParentAccountList:[],
            CientOwnerList:[],
            optiontypeCheckBox:'',
            RoleList:[],
            tempDisplayName:'',
            diplayNameWithCulture:[],
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [                                
                        {
                            astype: "TextField",
                            label: this.$t('Label Name'),
                            name: "Label",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },                            
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Number of characters allowed'),
                            name: "characters",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },                            
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Options'),
                            name: "Options",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },                            
                        }
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",                    
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick:this.Close
                }
            ],
        }
    },
    methods:{
        removeCultureRow: function (index) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Group")), function () {
                vm.diplayNameWithCulture.splice(index, 1);
            });
        },
        AssignCheckboxOptionsTypeToggleValue: function (event) {
                var vm=this;
                if (event.target.checked == true) {
                    vm.optiontypeCheckBox = 'HorizantalCheckBox';
                }
                else {
                    vm.optiontypeCheckBox = 'VerticalCheckBox';
                }
        },
        Close:function(){
            var vm=this;
            vm.$emit("Close");
        },
        ChangeFieldName: function (item) {
            // item.name = (this.tempDisplayName).split(' ').join("_");
            //  item.name = (this.tempDisplayName);
            //  $("#txtmodaldisplay_name").removeClass('is-invalid');
            //  $("#displayNameError").css('display', 'none');
            //  this.$validator.reset();
        },
        addMoreRowForCulture: function () {
            debugger
                var objectForCulture = {
                    localization_id: 0,
                    custom_field_id: 0,
                    lang_id: '',
                    localization_value: '',
                    //user_guide: ''
                }
                this.diplayNameWithCulture.push(objectForCulture);
        },
        onSubmit:function(item){
            var ths = this;
            var name='';
            var field_Name = (String(this.tempDisplayName).split(' ').join("_"));
            var ExistingfieldData = [];
            var ExistingClientData = [];
            ExistingfieldData = $.grep(this.$parent.nonDisplayField, function (tempItem, index1)
            {
            return tempItem.name.toLowerCase() == field_Name.toLowerCase();
            });
            if (ExistingfieldData.length <= 0) {
                        $.each(this.$options.parent.LayoutList, function (index, element) {
                            $.each(element.FieldData, function (index1, tempItem) {
                                if (tempItem.name.toLowerCase() == field_Name.toLowerCase())
                                    ExistingClientData.push(tempItem);
                            });
                        });
            }
            ths.$refs.AddFormBuilder.validate().then(result=>{
                if(result){
                            this.$options.parent.showModal = false;
                            item.cultureDetails = this.diplayNameWithCulture;
                            item.is_required = $("#chkis_required").prop("checked") == true ? 1 : 0;
                            item.is_email_template = $("#chkis_email_template").prop("checked") == true ? 1 : 0;
                            item.display_name = $("#txtmodaldisplay_name").val();
                            item.name = $("#txtmodaldisplay_name").val();
                            name=item.display_name;
                            var arr = [];
                            if ($("#txtpicklist_options").length) {
                                arr.push($("#txtpicklist_options").val());
                            }
                            console.log("item223213", item);
                            console.log("this.optiontype22", ths.optiontype);
                            console.log("item.optionsType11", item.optionsType);
                            if (ths.optiontype != undefined || item.optionsType==null) {
                                item.optionsType = ths.optiontype;
                            }
                            if (ths.optiontype == 'Vertical')
                            {
                                item.optionsType = null;
                            }
                           if (this.optiontypeCheckBox) {
                                item.ChkBoxoptionsType = this.optiontypeCheckBox;
                            }
                            if ($("#txtpicklist_options2").length) {
                                var totalOptions = $("#txtpicklist_options2").length;
                                for (var i = 0; i< ths.diplayNameWithCulture.length; i++) {
                                    arr.push(ths.diplayNameWithCulture[i].localization_value);
                                    }
                            }
                            item.picklist_options = arr;

                            if ($("#txtmodallength").length) {
                                item.length = $("#txtmodallength").val();
                            }
                            //debugger;
                           // this.$options.parent.LayoutList[ths.parentHeaderIndex].SectionData[ths.parentIndex].FieldData[ths.childIndex] = item;
                            if (item.group_id > 0) {
                                item.is_updated = 1;
                            }
                            this.$emit("SaveProperties",item,name)
                            setTimeout("CheckBoxBootstrap()", 100);
                            setTimeout("RadioButtonBootstrap()", 500);
                } else {
                    if ((ExistingfieldData.length > 0 && ExistingClientData.length > 0) || (ExistingClientData.length > 1 && item.name == field_Name)) {
                        $("#txtmodaldisplay_name").addClass('is-invalid');
                        $("#displayNameError").css('display', 'block');
                    }
                }
            });
        }
    }
}
</script>